<template>
    <div>
        <p>{{getWax}}</p>
        <p>Create Schema</p>
        <p>Schema_name</p>
        <input v-model="Schema_name" placeholder="Schema name">
        <p>Attribute Name</p><br />
        <p>name</p><br />
        <p>img</p>
        <p>Attribute Type</p><br />
        <p>text</p><br />
        <p>img</p>
        <table class="table">
            <thead>
                <tr>
                    <td><strong>Title</strong></td>
                    <td><strong>Description</strong></td>
                    <td><strong>File</strong></td>
                    <td></td>
                    <tbody>
                        <tr :v-for="(row, index) in rows" :key="row.Title">
                            <td><input type="text" v-model="row.Title"></td>
                            <td><input type="text" v-model="row.description"></td>
                            <td>
                                <label class="fileContainer">
                                    {{row.file.name}}
                                    <input type="file" @change="setFilename($event, row)" :id="index">
                                </label>
                            </td>
                            <td>
                                <a v-on:click="removeElement(index);" style="cursor: pointer">Remove</a>
                            </td>
                        </tr>
                    </tbody>
                </tr>
            </thead>
        </table>
        <div>
            <button class="button btn-primary" @click="addRow">Add row</button>
        </div>
    </div>
</template>
<script>
import { mapGetters } from 'vuex'

export default {
    name:'createSchema',
    props:['wax','assetID'],
    data(){
        
        return{
        
            rows:[],
            countFirstboxInputID:0,
            countSecondboxInputID:0,
            Schema_name:"",
            schemaformat:[{
                "name":"name",
                "type":"string"
              },
              {
                  "name":"img",
                  "type":"image"
              }]
        }
    },
    computed: {
      ...mapGetters([
        'getWax'
      ]),
      collectionname:function(){
          return this.collectionname
      },
      schemaFormat:function(){
         return this.schemaformat;
      }
    },
    methods:{
        addRow: function(){
            var elem = document.createElement('tr');
                    this.rows.push({
                        title: "",
                        description: "",
                        file: {
                            name: 'Choose File'
                        }
                    });
                    return elem
        },
        removeElement: function(index) {
                    this.rows.splice(index, 1);
        },
         setFilename: function(event, row) {
                    var file = event.target.files[0];
                    row.file = file
        },
       async CreateSchema(){
            if(!this.getWax.api){
               return console.log("Need to login first")
           }
           try {
               this.result=await this.getWax.api.transact({
                   actions:[{
                    account: 'atomicassets',
                    name: 'createschema',
                    authorization: [{
                        actor: this.wax.userAccount,
                        permission:'active',
                    }],
                    data: {
                        authorized_creator:this.getWax.userAccount,
                        collection_name:this.collectionname,
                        Schema_name:this.schemaFormat,
                    },
                   }]
               },
               {
                   blockBehind: 3,
                   expireSecond: 30
               })
               console.log(this.result);
               console.log("THIS AUTHOR IS "+this.author);
           }
           catch(e){
               this.result=e
               console.log(e)
           }       
       }
    }
}
</script>